import React, {useEffect, useState} from 'react';

const dropdownStyles = `
  padding: 10px;
  border: 1px solid #E5E5E5;
  border-top: none;
  width: 300px;
  background-color: #FFF;
  margin: 0 auto;
`;

export default function Dropdown({ children, onRequestClose }) {
    let [shown, setShown] = useState(false)
    useEffect(() => {
        setTimeout(() => {
            setShown(true)
        }, 50)
    }, [])
  return (
      <div className={``}>
            <div className={`modal absolute bg-white w-full h-full top-0 rounded-lg flex flex-col justify-center items-center bg-gray-800 bg-opacity-95 transition ${shown ? 'opacity-100' : 'opacity-0' }`}>
                {children}
                <button onClick={onRequestClose} className={'text-red-500 mt-5 px-3 py-2 rounded-lg hover:bg-gray-700'}>Cancel</button>
            </div>
      </div>
        
  );
}